let countdown;
export default {
	data() {
		return {
			time:5,
		}
	},
	onLoad() { 
		this.countdown = setInterval(()=>{
			if(this.time>1){
				this.time-=1;
			}else{
				clearInterval(this.countdown);
			}
		},1000);
		setTimeout(()=>{
			uni.switchTab({
				url:'/pages/tabber/home/home'
			})
		},5000);
	},
	methods: {
		
	}
}
